:root{
  --background-color: white;
  --main-color-1: black;
  --color-green: #00fdc0;
}

.underline, .activeLink {
  border-bottom: 4px solid var(--color-green);
  display: inline-block;
  line-height: 0.90;
}

button:focus {
  outline:0;
}

html{
  background-color: var(--background-color);
  color: var(--main-color-1);
  transition: background-color 0.5s ease;
  scroll-behavior: smooth;
}

.App {
  text-align: center;
  background-color: var(--background-color);
  color: var(--main-color-1);
  transition: background-color 0.5s ease;
}


.author{
  border: solid;
  width: 50%;
  margin: 1rem auto;
  padding: 1rem;
  text-align: left;
}

@media only screen and (max-width: 800px) {
  .author {
    width: 80%;
  }
}

.author button, .pageButtons button, .lightDark button, .Day button{
  border: solid;
  background-color: var(--background-color);
  color: var(--main-color-1);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: .2rem;
  transition: background-color 0.5s ease;
}

.author  button:hover, .pageButtons button:hover, .lightDark button:hover .Day button:hover{
  background-color: var(--main-color-1);
  color: var(--background-color);
  border: solid var(--main-color-1);
  transition: .3s all ease-in-out;
}

.pageButtons {
  width: 50%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

@media only screen and (max-width: 800px) {
  .pageButtons {
    width: 80%;
  }
}

.pageButtons button{
  margin: 1rem;
  min-width: 30%;
}

.searchMenu {
  display: flex;
  border: solid ;
  width: 50%;
  margin: auto;
  padding: 1rem;
  flex-wrap: wrap;
  justify-content: center;

}

@media only screen and (max-width: 800px) {
  .searchMenu {
    width: 80%;
  }
}

.searchInput, .dropMenu{
  border: solid var(--main-color-1);
  background-color: var(--background-color);
  color: var(--main-color-1);
  transition: background-color 0.5s ease;
  border-radius: .2rem;
  padding: .2rem;
}

.dropMenu{
  margin: 0.5rem 1rem;
}

.searchInput:focus, .dropMenu:focus{
  outline:0;
  color: var(--main-color-1);
}

.searchBar {
  width: 100%;
  display: flex;
  align-items: center;
}


.SearchText{
  width: 30%;
  text-align: right;
}

.AuthorSearchButton{
  height: 1.8rem;
}


.searchInput{
  width: 50%;
  height: 1rem;
  margin: 1rem;
}

.loader {
  border: 4px solid var(--main-color-1); 
  border-top: 4px solid var(--background-color); 
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
  margin: 1rem auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.footer{
  width: 50%;
  margin: auto;
  padding: 1rem;
  border-top: solid;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 800px) {
  .footer {
    width: 80%;
  }
}

.NavMenu{
  width: 50%;
  margin: 1rem auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  padding: 0 1rem;
  position: sticky;
  top: 0;
  background-color: var(--background-color);
  border-left: solid var(--background-color);
  border-right: solid var(--background-color);
  border-bottom: solid;
  transition: background-color 0.5s ease;
}

@media only screen and (max-width: 800px) {
  .NavMenu {
    width: 90%;
    justify-content: center;
  }
}

.NavContain {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0!important;
  margin-bottom: 1rem;
}

@media only screen and (max-width: 800px) {
  .NavContain {
    margin-bottom: 0;
  }
}

.NavContain li, .NavMenu button {
  list-style-type: none;
  border: solid var(--main-color-1);
  border-radius: .2rem;
  padding: .2rem 1rem;
  margin: 1rem;
  text-align: center;
}

@media only screen and (max-width: 1030px) {
  .NavContain li, .NavMenu button {
    width: 100%;
  }
}

.NavMenu button {
  background-color: var(--background-color);
  color: var(--main-color-1);
  padding: .4rem 1rem;
  align-items: center;
}

.NavContain li a {
  color: var(--main-color-1);
  text-decoration: none;
}

.Collapsible__contentInner{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 90%;
    border: solid;
    margin: auto;
}

.Collapsible__contentOuter{
  margin-bottom: 1rem;
}

.paperDisplay{
  border-bottom: solid;
}

.papersDisplayBox:nth-last-child(1) {
  border-bottom: none;
}

.Collapsible{
  display: flex;
  flex-wrap: wrap;
}

.Day{
  border: solid;
  width: 70%;
  margin: 1rem auto;
  text-align: left;
  padding: 1rem;
}

@media only screen and (max-width: 800px) {
  .Day {
    width: 85%;
  }
}

.slot, .slotBreak{
  border: solid;
  border-width: 0px 0px 2px 0px;
  width: calc(100% - 2rem);
  padding: 0 0 1rem 0;
  margin: 0rem auto;
  text-align: left;
}

.slotBreak{
    padding: 0.5rem 0 0.5rem 0;
}

.sessionsCont{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.SessionsBox{
  width: 39%;
  border: solid;
  padding: 1rem;
  margin: 1rem;
  transition: width 0.5s ease;
}

@media only screen and (max-width: 800px) {
  .sessionsCont{
    display: block;
  }
  .SessionsBox {
    width: 90%;
    margin: 1rem auto;
  }
}

@keyframes wobble {
  0% { transform: rotate(0deg); }
  25% { transform: rotate(40deg); }
  50% { transform: rotate(0deg); }
  75% { transform: rotate(-40deg); }
  100% { transform: rotate(0deg); }
}

.NotFoundFace{
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  animation: wobble 3s linear infinite;
}

.presentation{
  border: solid;
  padding: 1rem;
  margin: 1rem 0;
}

.updateSession{
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  margin: auto;
  border: solid;
  padding: 1rem;
  justify-content: center;
  margin-bottom: 1rem;
}

@media only screen and (max-width: 800px) {
  .updateSession{
    width: 90%;
  }
}

.updateSession h2, .updateSession p {
  width: 100%;
  margin: 0;
}
.updateSession textarea {
  width: 100%;
  border: solid;
  margin: 1rem;
}

.UpdateSessionButton, .LogOutButton, .LoginArea button, .SignUpArea button, #AuthorSearchButton{
  border: solid;
  background-color: var(--background-color);
  color: var(--main-color-1);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: .2rem;
  transition: background-color 0.5s ease;
}

.UpdateSessionButton:hover, .LogOutButton:hover, #AuthorSearchButton:hover{
  background-color: var(--main-color-1);
  color: var(--background-color);
  border: solid var(--main-color-1);
  transition: .3s all ease-in-out;
}

.adminArea{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.AdminTop{
  width: 30%;
  margin: auto;
  padding: 1rem;
}

@media only screen and (max-width: 800px){
  .AdminTop {
      width: 80%;
  }
}

.LoginArea, .SignUpArea{
  border: solid;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 1rem;
}

.LoginArea input, .SignUpArea input{
  width: 90%;
  margin: .5rem;
  padding: .2rem;
  border: solid;
  background-color: var(--background-color);
  color:var(--main-color-1);
}
.LoginArea button, .SignUpArea button{
  margin: .5rem;
}

.LogOutButton{
  margin: 1rem auto;
}

.loginButton:disabled {
  background-color: grey;
}

.adminSearch{
  width: 50%;
  margin: auto;
}

@media only screen and (max-width: 800px){
  .adminSearch {
      width: 100%;
  }
}


